import Vue from 'vue';
import Router from 'vue-router';
import Mian from './components/mian.vue';
import Detail from './components/detail.vue';
Vue.use(Router);
let  router = new Router({
	mode:'history',
	routes:[
		{
			path:'/',
			name:'Mian',
			component:Mian,
		},
		{
			path:'/detail',
			name:'detail',
			component:Detail,
		}
	]
})

export default router