<template>
	<div id="leaderBoard">
		<div>
			子页面 count {{count}}
		</div>
		<div>
			子页面 name {{name}}
		</div>
		
                <!-- 添加 排行变化动画 -->
                <transition-group name="leaderBoard-item"
                    mode="out-in">
                    <div v-for='(item,index) in list' :key="item.text" class='leaderBoard_item'>
						{{item.text}} {{item.grade}}
                    </div>
                </transition-group>
				<button @click="addGrade">增加分数</button>
	</div>
</template>

<script>
	import { store,mutation} from './store.js'
	export default{
		name:'storeComponent',
		data(){
			return{
				list:[
					{ id: 1, text: 'Buy eggs', grade: 5 },
					{ id: 2, text: 'Pay bills', grade: 4 },
					{ id: 3, text: 'Invite friends over', grade: 3 },
					{ id: 4, text: 'Fix the TV', grade: 1 },
				],
			}
		},
		computed:{
			count(){
				return store.count
			},
			name(){
				return store.name
			},
		},
		methods:{
			addGrade(){
                this.list[3].grade = this.list[3].grade + 1;
				this.list.sort((a, b) => b.grade - a.grade);
			}
		}
	}
</script>


<style >

.leaderBoard-item-move {
    transition: transform 1s;
    /* transition: all 0.3s; */
}
</style>

