<template>
	<div>
		<HelloWorld msg="Welcome to Your Vue.js App"/>
		<listPage :data="tableData"/>
		<input v-focus  v-demo:foo.a.b="message"/>
		{{message | toUpp("args",'arg2') | toLay}}
		<storeComponent />
		<router-link to="/detail" ><h5>跳转到详情页</h5>   </router-link>
	</div>
</template>

<script>
	let  mixin = {
		created:function(){
			this.hello();
		},
		methods:{
			hello:function(){
				console.log('hello mixin');
			},
		}		
	}
	import HelloWorld from '@/components/HelloWorld.vue'
	import storeComponent from '@/components/storeComponent'
	// import listPage from './components/list-page.vue'
		import {listPage} from 'list-page';
	
	export default {
	  name: 'app',
		mixins:[mixin,],
		filters:{
			toUpp:function(value){
				if(!value) return
				return value.toUpperCase();
			},
			toLay:function(value){
				return value.toLowerCase();
			}
		},
		directives:{
			focus:{
				inserted : function(el,binding,vnode,){
						el.focus();
				}
			},
			demo:{
				bind:function(el,binding,vnode,oldVnode){
					console.log(el,binding,vnode,oldVnode);
				},
				inserted:function(){
					
				}
			}
		},
		data(){
			return {
					message:'Hello',
					tableData: [{
				  date:'2016-05-02',
				  name:'王小虎',
				  address:'上海市普陀区金沙江路 1518 弄'
				},{
				  date:'2016-05-02',
				  name:'王小虎',
				  address:'上海市普陀区金沙江路 1518 弄'
				},{
				  date:'2016-05-02',
				  name:'王小虎',
				  address:'上海市普陀区金沙江路 1518 弄'
				},{
				  date:'2016-05-02',
				  name:'王小虎',
				  address:'上海市普陀区金沙江路 1518 弄'
				},],
			}
		},
	  components: {
		HelloWorld,
			storeComponent,
			listPage
	  }
	}
</script>

