<template>
	<div>
		<div>
			count {{count}}
		</div>
		<div>
			name {{name}}
		</div>
		<div>
			<button @click="setCount(4)">修改 count</button>
		</div>
		<div>
			<button @click="changeName('ccy')">改变 name</button>
		</div>
	</div>
</template>

<script>
import { store,mutation} from './store.js'
export default{
	name:'storeComponent',
	computed:{
		count(){
			return store.count
		},
		name(){
			return store.name
		}
	},
	methods:{
		setCount:mutation.setCount,
		changeName:mutation.changeName,
	}
}
</script>

